<template>
  <b-navbar type="dark" variant="dark" fixed="top">
    <b-navbar-nav>
      <b-navbar-brand>One Stop Shop</b-navbar-brand>
      <b-nav-item href="/">Home</b-nav-item>
      <!-- Navbar dropdowns -->
    </b-navbar-nav>
    <b-navbar-nav class="ml-auto">
      <b-nav-item v-if="!this.$store.getters.getStatus"
        ><router-link :to="{ path: '/Login' }"
          >Login/Signup</router-link
        ></b-nav-item
      >
      <b-button v-if="this.$store.getters.getStatus"  v-b-toggle.sidebar-right>Cart</b-button>
      <b-nav-item v-if="this.$store.getters.getStatus" >Hello, <b>{{this.$store.getters.getUser.displayName}}</b></b-nav-item>
      <b-nav-item v-if="this.$store.getters.getStatus" @click="logOut"
        >Logout</b-nav-item
      >
    </b-navbar-nav>
  </b-navbar>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Header",
  data() {
    return {
      user: {},
    };
  },
  mounted() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.commit("updateUser", user);
        this.$store.commit("updateStatus", true);
        // User is signed in.
      } else {
        // No user is signed in.
        this.$store.commit("updateUser", {});
        this.$store.commit("updateStatus", false);
        this.$store.commit("updateCart", {})
        console.log("Wiped out stuff")
      }
    });
  },
  computed: {},
  methods: {
    logOut() {
      firebase.auth().signOut();
    },
  },
};
</script>

